
export default {
  namespace: 'mobile',
  state: {},
  reducers: {},
  effects: {
    *getMessages({
      payload,
    }, {call, put}) {
      const data = yield call(queryMessages, parse(payload));
      if (data.success) {
        yield put({
          type: 'updateState',
          payload: {
            messages: data.result,
          },
        });
      }
    },
  },
  subscriptions: {},
};
